/**
 * @namespace biigle.projects
 * @ngdoc controller
 * @name ProjectDeleteController
 * @memberOf biigle.projects
 * @description Initiates the deletion confirmation modal
 * @example

 */
angular.module('biigle.projects').controller('ProjectDeleteController', function ($scope, $modal, $attrs, msg) {
		"use strict";

		var success = function () {
			$scope.redirectToDashboard($attrs.successMsg);
		};

		var error = function () {
			msg.danger($attrs.errorMsg);
		};

		$scope.submit = function () {
			var modalInstance = $modal.open({
				templateUrl: 'confirmDeleteModal.html',
				size: 'sm',
				controller: 'ProjectDeleteModalController',
				scope: $scope
			});

			modalInstance.result.then(function (result) {
				switch (result) {
					case 'success':
						success();
						break;
					case 'error':
						error();
						break;
				}
			});
		};
	}
);
