/**
 * @namespace biigle.projects
 * @ngdoc controller
 * @name ProjectDeleteModalController
 * @memberOf biigle.projects
 * @description Handles the confirmation of deletion of a project.
 * @example

 */
angular.module('biigle.projects').controller('ProjectDeleteModalController', function ($scope, Project) {
		"use strict";

		$scope.force = false;

		var deleteSuccess = function (response) {
			$scope.$close('success');
		};

		var deleteError = function(response) {
			if (response.status === 400) {
				$scope.force = true;
			} else {
				$scope.$close('error');
			}
		};

		$scope.delete = function () {
			var params = $scope.force ? {force: true} : {};
			$scope.project.$delete(params, deleteSuccess, deleteError);
		};
	}
);
